.container {
  margin: 0 auto;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media only screen and (min-width: 992px) {
  .container {
    width: 960px;
  }
}
.row,
.row-fluid {
  display: block;
  position: relative;
  margin: 1% 0;
  zoom: 1;
}
.row:before,
.row-fluid:before,
.row:after,
.row-fluid:after {
  content: ".";
  visibility: hidden;
  display: table;
  font-size: 0;
  line-height: 0;
}
.row:after,
.row-fluid:after {
  clear: both;
}
.col {
  width: 100%;
}
@media only screen and (min-width: 480px) and (max-width: 767px) {
  .col.span-50_100,
  .col.span-1,
  .col.span-2,
  .col.span-3,
  .col.span-4,
  .col.span-5,
  .col.span-6 {
    float: left;
    width: 49%;
    padding: 0 0 0 2%;
  }
  .col.span-100_100,
  .col.span-7,
  .col.span-8,
  .col.span-9,
  .col.span-10,
  .col.span-11,
  .col.span-12 {
    clear: both;
  }
  .col:nth-child(odd) {
    clear: both;
    padding-left: 0;
  }
}
